import React, { useContext } from 'react'
import styles from './navButtons.module.css'
import { Link } from "gatsby"
import AppContext from '../../contextAPI/AppContext'

const NavButtons = (props) => {
    const receivedAppContext = useContext(AppContext)
    const classNamesLeft = [styles.button, styles.left].join(' ')
    const classNamesRight = [styles.button, styles.right].join(' ')
    return (
        <React.Fragment>
            <Link to={props.prev} 
                className={classNamesLeft} 
                state={{
                    currentDisplayRef: receivedAppContext.displayRef,
                    currentInterlinear: receivedAppContext.interlinear}}
            >
                <i className="pi pi-chevron-left" style={{fontSize: '0.5em'}}></i></Link>
            <Link to={props.next} 
                className={classNamesRight} 
                state={{
                    currentDisplayRef: receivedAppContext.displayRef,
                    currentInterlinear: receivedAppContext.interlinear}}
            >
                <i className="pi pi-chevron-right" style={{fontSize: '0.5em'}}></i></Link>
        </React.Fragment>
    )
}

export default NavButtons;