import { Link } from "gatsby"
import PropTypes from "prop-types"
import React, { useContext } from "react"
import styles from './header.module.css'
import AppContext from '../../contextAPI/AppContext'
import { navigate } from "@reach/router";

const Header = ({ siteTitle, showIcons }) => {
  // This controls the visibility of chapter and verse references
  const receivedAppContext = useContext(AppContext);
  const interlinearOption = (receivedAppContext.version === 'GNA' || receivedAppContext.version === 'WLC') ? true : false
  // change all updateDisplayRef to manually set true or false

  return (
  <header className={styles.header}>
    <div className={styles.topNav}>
      <div className={styles.topNavBarButtons} onClick={receivedAppContext.updateMenu}>
        <i className="pi pi-bars" style={{fontSize: '2em', color: 'white'}}></i>
      </div>
      <Link
        to="/"
        style={{
          color: `white`,
          textDecoration: `none`,
          flex: '10'
        }}
      >
        {siteTitle}
      </Link>

      {(interlinearOption && showIcons) ? (
        <div className={styles.topNavBarButtons} onClick={() => receivedAppContext.updateInterlinear(!receivedAppContext.interlinear)}>
          <i class="pi pi-info-circle" style={{fontSize: '2em', color: 'white'}}></i>
        </div>
      ) : null}
      {(showIcons) ? (<div className={styles.topNavBarButtons} onClick={() => receivedAppContext.updateDisplayRef(!receivedAppContext.displayRef)}>
        {(receivedAppContext.displayRef) ? (<i class="pi pi-eye" style={{fontSize: '2em', color: 'white'}}></i>) : 
        (<i class="pi pi-eye-slash" style={{fontSize: '2em', color: 'white'}}></i>)}
      </div>) : null}
      {(showIcons) ? (<div className={styles.topNavBarButtons} onClick={() => {
        receivedAppContext.updateDisplayRef(true)
        receivedAppContext.updateSearchID(`Luk-4-16`)
        // We need to define which verse goes into which segment (a new JSON file)
        navigate(`/${receivedAppContext.language}/${receivedAppContext.version}/S286`,
          { state: { currentInterlinear: receivedAppContext.interlinear },
          })}}>
        <i class="pi pi-search" style={{fontSize: '2em', color: 'white'}}></i>
      </div>) : null}
    </div>
  </header>
)}

Header.propTypes = {
  siteTitle: PropTypes.string,
}

Header.defaultProps = {
  siteTitle: ``,
}

export default Header
